}

#decades .decade > .title-screen > .header .svg-char > .among-us {
  width: 20vmin;
  transform: scale(2.5)translateX(-480px);
}

#decades .decade > .title-screen > .header .svg-char:has( > .pacman) {
  transform: translateY(0);
}

#decades .decade > .title-screen > .header .svg-char > .pacman {
  z-index: 5555;
  width: 20vmin;
  transform: scale(2.5)translateX(-480px);
}

#decades .decade > .title-screen > .header .svg-char:has( > .mario-coin) {
  opacity: 0;
  gap: .75rem;
  padding: .75rem;
}

#decades .decade > .title-screen > .header .svg-char > .mario-coin {
  width: 13.75vmin;
}

#decades .decade > .title-screen > .header .svg-char:has( > .ocarina-note) {
  opacity: 0;
  gap: .75rem;
  padding: .75rem;
}

#decades .decade > .title-screen > .header .svg-char > .ocarina-note {
  width: 12.75vmin;
  transform: scale(.75);
}

#decades .decade > .title-screen > .header > .title {
  -webkit-user-select: none;
  user-select: none;
  z-index: 7777;
}

#decade-1970 {
  color: #fff;
  background-color: #000;
  border-color: #fff;
}

#decade-1970 .stars-container {
  width: 100%;
  height: 110vh;
  display: flex;
  position: absolute;
  left: 0;
  overflow: hidden;
}

#decade-1970 .stars-container > .stars {
  background: none;
  position: absolute;
  overflow: hidden;
}

#decade-1970 .stars-container > .stars.small {
  width: 1px;
